body {
  color: #000;
  margin: 0;
  padding: 0;
  font-family: DM Sans, sans-serif;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  z-index: 1;
  width: 100%;
  height: 502px;
  background-color: #ffb8ca;
  background-image: url("mobileHeaderPic2.03da9c9e.png"), url("mobileHeaderEllipse.abf54114.png");
  background-position: 117px 92px, 36px 83px;
  background-repeat: no-repeat;
  padding: 6px 20px 0;
  animation: 3s ease-in icecream;
  position: relative;
}

.header__menu {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__nav {
  display: none;
}

.header__btn-buynow {
  padding: 0;
  display: none;
}

.header__burger-icon {
  cursor: pointer;
}

.header__head {
  letter-spacing: .04em;
  color: #fff;
  margin-top: 135px;
  font-family: Titan One, cursive;
  font-size: 26px;
  font-weight: 400;
  line-height: 30px;
}

.header__head-colorpart {
  color: #d41443;
  font-size: 48px;
  line-height: 46px;
}

.header__head-bttns {
  gap: 15px;
  margin-top: 21px;
  display: flex;
}

.header__bttn {
  height: 24px;
  cursor: pointer;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  transition: border-radius .25s;
  display: flex;
}

.header__bttn:hover {
  border-radius: 50px 10px;
}

.header__bttn--products {
  width: 63px;
  background-color: #d41443;
}

.header__bttn--how {
  width: 89px;
  background-color: #fff;
}

.header__head-link--1 {
  color: #fff;
  font-size: 8px;
  line-height: 10px;
}

.header__head-link--2 {
  color: #d41443;
  font-size: 8px;
  line-height: 10px;
}

.header__milk, .header__count {
  display: none;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
